* {
  background: #b8d8d8;
}

table {
  border-radius: 10px;
  max-height: 30vw;
  max-width: 30vw;
  position: relative;
  margin-top: 5vw;
  margin-right: auto;
  margin-left: auto;
  background: #7a9e9f;
  padding: 1vw;
}

td {
  max-height: 10vw;
  max-width: 10vw;
  min-height: 10vw;
  min-width: 10vw;
  width: 10vw;
  height: 10vw;
  background: #eef5db;
  text-align: center;
}

article {
  border-radius: 1px;
  width: 8.5vw;
  height: 8.5vw;
  border-width: 5px;
  border-style: solid;
  border-color: black;
  background: #eef5db;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.Clicked {
  border-color: rgb(0, 255, 0);
}

div .pieces {
  margin-top: 50px;
  height: 10vw;
  display: flex;
  flex-direction: grid;
  justify-content: center;
  align-content: center;
}

img {
  width: 20.45vw;
  height: 20.45vw;
  background: transparent;
  /* border-style: solid;
  border-width: 2px;
  border-color: black; */
  pointer-events: none;
  transform: rotate(90deg);
}
